.box {
  position: absolute;
  top: 5px;
  right: 5px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 14px;
  height: 14px;
}
.line-1 {
  position: relative;
  width: 3px;
  height: 14px;
  background-color: #fcca25;
  animation: go-up-down 0.7s infinite alternate;
}
.line-2 {
  position: relative;
  width: 3px;
  height: 10px;
  background-color: #fcca25;
  animation: go-up-down 0.6s infinite alternate;
}
.line-3 {
  position: relative;
  width: 3px;
  height: 8px;
  background-color: #fcca25;
  animation: go-up-down 0.5s infinite alternate;
}
.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
@-moz-keyframes go-up-down {
  0% {
    height: 0;
  }
  100% {
    height: 14px;
  }
}
@-webkit-keyframes go-up-down {
  0% {
    height: 0;
  }
  100% {
    height: 14px;
  }
}
@-o-keyframes go-up-down {
  0% {
    height: 0;
  }
  100% {
    height: 14px;
  }
}
@keyframes go-up-down {
  0% {
    height: 0;
  }
  100% {
    height: 14px;
  }
}
