.header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  outline: 1px solid transparent;
  border-radius: 10px;
  box-shadow: inset 0 -3em 3em rgba(0,0,0,0.2), 0 0 0 1px rgba(0,173,176,0.553), 0.3em 0.3em 1em rgba(0,0,0,0.2);
}
.logo {
  max-height: 80px;
  max-width: 80px;
  width: 100%;
  border-radius: 40px;
  margin: 8px 0;
}
